.buttons {
    margin-top: 1.5rem;
    margin-bottom: 2rem;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.button {
    background-image: linear-gradient(to top, #4481eb 0%, #04befe 100%);
    color: white;
    font-size: 1.1rem;
    padding: .3rem 1.5rem;
    border-radius: 4px;
    border: 0px;
}

.button:hover {
    cursor: pointer;
    background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
}

.button:active {
    transform: translateY(2px);   
}
.table {
    width: 95%;
    margin-bottom: 1rem;
    text-align: center;
}

.title {
    background-color: coral;
    color: purple;
    border: 2px solid white;
    font-size: 1.5rem;
}

.table td {
    font-size: 1.1rem;
}



